export default {
  data() {
    return {
      list: [],
      allLoaded: false,
      autoFill:false,
      bottomStatus: "",
      wrapperHeight: 0,
      topStatus: "",
      translate: 0,
      moveTranslate: 0
    };
  },

  methods: {
    handleBottomChange(status) {
      this.bottomStatus = status;
    },

    loadBottom() {
      setTimeout(() => {
        this.axios.get("/api/withdrawList").then(res => {
          this.list.push(...res.data.withdrawList);
        });
        this.$refs.loadmore.onBottomLoaded();
      }, 1500);
    },
    handleTopChange(status) {
      this.moveTranslate = 1;
      this.topStatus = status;
    },
    loadTop() {
      setTimeout(() => {
        this.axios.get("/api/withdrawList").then(res => {
          this.list = res.data.withdrawList;
        });
      }, 1500);
    }
  },

  created() {
    this.axios.get(this.api + "/wx/getUser").then(res => {
      if (res.data.statu == 1) {
        this.user = res.data.user;
      } else {
        window.location = this.api + "/wx/login?goback=walletDetail";
      }
    });
    this.axios.get("/api/withdrawList").then(res => {
      this.list = res.data.withdrawList;
    });
  },

  mounted() {
    this.wrapperHeight =
      document.documentElement.clientHeight -
      this.$refs.wrapper.getBoundingClientRect().top;
  }
};



